import '../vendor/polyfills/from';

const elems = document.querySelectorAll( '.js-spin' );
const defaults = {
  min: 1,
  max: 99,
  upClassName: 'spin__up',
  downClassName: 'spin__down',
};
const KEY_ARROW_UP = 38;
const KEY_ARROW_DOWN = 40;
const CHANGE_EVENT = new CustomEvent( 'change' );

[ ...elems ].forEach( ( el ) => {
  const input = el.querySelector( 'input' );

  if ( input !== null ) {
    createSpin( el, input );
  }
} );

function createSpin( el, input ) {
  const min = +el.getAttribute( 'data-spin-min' ) || defaults.min;
  const max = +el.getAttribute( 'data-spin-max' ) || defaults.max;
  const upBtn = document.createElement( 'button' );
  const downBtn = document.createElement( 'button' );

  let value = +input.value || min;

  // up button
  upBtn.setAttribute( 'type', 'button' );
  upBtn.setAttribute( 'class', defaults.upClassName );

  // down button
  downBtn.setAttribute( 'type', 'button' );
  downBtn.setAttribute( 'class', defaults.downClassName );

  el.appendChild( upBtn );
  el.appendChild( downBtn );

  input.addEventListener( 'keyup', changeValue, false );

  upBtn.addEventListener( 'click', add, false );
  downBtn.addEventListener( 'click', subtract, false );

  function checkValue( newValue ) {
    let val = newValue || min;

    val = val > max ? max : val;
    val = val < min ? min : val;

    input.value = value = val;

    input.dispatchEvent( CHANGE_EVENT );
  }

  function changeValue( e ) {
    const code = e.keyCode ? e.keyCode : e.which;

    if ( code === KEY_ARROW_UP ) {
      add();
    } else if ( code === KEY_ARROW_DOWN ) {
      subtract();
    } else {
      checkValue( +input.value );
    }
  }

  function add() {
    checkValue( ++value );
  }

  function subtract() {
    checkValue( --value );
  }
}
