if ( 'IntersectionObserver' in window &&
  'IntersectionObserverEntry' in window &&
  'intersectionRatio' in window.IntersectionObserverEntry.prototype &&
  !window.IntersectionObserverPolyfill ) {
  productImages();
}

/**
 * product images
 */
function productImages() {
  const config = {
    // root: null,
    threshold: [ ...Array( 21 ).keys() ].map( ( x ) => x / 20 ), // eslint-disable-line no-magic-numbers
  };

  const $leftBox = document.querySelector( '.js-product-left-image' );
  const $leftImage = $leftBox ? $leftBox.querySelector( 'img' ) : null;
  const $centerBox = document.querySelector( '.js-product-center-image' );
  const $centerImage = $centerBox ? $centerBox.querySelector( 'img' ) : null;
  const $rightBox = document.querySelector( '.js-product-right-image' );
  const $rightImage = $rightBox ? $rightBox.querySelector( 'img' ) : null;

  let leftRatioOld = 0;

  const observer = new IntersectionObserver( ( entries ) => {
    entries.forEach( ( entry ) => {
      const vh = entry.rootBounds.height;
      const ratio = entry.intersectionRatio > 1 ? 1 : entry.intersectionRatio;

      if ( entry.target.classList.contains( 'js-product-left-image' ) ) {
        const isUponMiddle = entry.boundingClientRect.top < ( vh / 2 );

        if ( isUponMiddle ) {
          entry.target.style.opacity = ratio;
          $leftImage.style.transform = `translate3d(${ ( ratio * 100 ) - 100 }%, 0, 0)`;
        } else {
          $leftImage.style.transform = 'translate3d(-100%, 0, 0)';
        }

      } else if ( entry.target.classList.contains( 'js-product-right-image' ) ) {
        const isUponMiddle = entry.boundingClientRect.top < ( vh / 2 );

        if ( isUponMiddle ) {
          entry.target.style.opacity = ratio;
          $rightImage.style.transform = `translate3d(${ 100 - ( ratio * 100 ) }%, 0, 0)`;
        } else {
          $rightImage.style.transform = 'translate3d(100%, 0, 0)';
        }

      } else if ( entry.target.classList.contains( 'js-product-center-image' ) ) {
        if ( entry.boundingClientRect.top < 0 ) {
          entry.target.style.opacity = entry.intersectionRatio;
        } else {
          entry.target.style.opacity = 1;
        }
      }
      // } else if ( entry.target.classList.contains( 'js-product-center-image' ) && entry.boundingClientRect.top >= 0 ) {
      //   entry.target.style.opacity = ratio;
      //   $centerImage.style.transform = `translate3d(0, ${ 100 - ( ratio * 100 ) }%, 0)`;
      // }
    } );
  }, config );

  if ( $leftBox ) {
    [ $leftBox, $centerBox, $rightBox ].forEach( ( $el ) => observer.observe( $el ) );
  }
}
