import '../vendor/polyfills/custom-event-polyfill';
import '../vendor/polyfills/from';

const $selects = document.querySelectorAll( 'select' );

const selectClasses = {
  select: 'select',
  open: 'is-open',
  box: 'select__box',
  value: 'select__value',
  list: 'select__list',
  item: 'select__item',
  itemActive: 'is-active',
};

const eventSelectChange = new CustomEvent( 'change' );

let $activeSelect = null;

[ ...$selects ].forEach( ( $select ) => {
  const select = getCustomSelectHtml( $select );
  let $customSelect = null;
  let $currentValue = null;
  let $options = null;

  $select.insertAdjacentHTML( 'beforeBegin', select );
  $select.style.display = 'none';

  $customSelect = $select.parentNode.querySelector( `.${ selectClasses.select }` );
  $currentValue = $select.parentNode.querySelector( `.${ selectClasses.value }` );
  $options = $select.parentNode.querySelectorAll( 'li' );

  $currentValue.addEventListener( 'click', toggleVisibility );

  [ ...$options ].forEach( ( $option ) => $option.addEventListener( 'click', selectOption ) );

  function toggleVisibility() {
    if ( $customSelect.classList.contains( selectClasses.open ) ) {
      hideSelect();
    } else {
      hideSelect();

      $activeSelect = $customSelect;
      $customSelect.classList.add( selectClasses.open );
    }
  }

  function selectOption( event ) {
    const $selectedOption = $select.querySelector( `option[value="${ event.target.getAttribute( 'data-value' ) }"]` );
    const prevValue = $select.value;

    if ( $selectedOption ) {
      $selectedOption.selected = true;
    }

    // select new value
    if ( prevValue !== $select.value ) {
      $currentValue.innerHTML = event.target.innerHTML;

      [ ...$options ].forEach( ( $option ) => $option.classList.remove( selectClasses.itemActive ) );

      $select.dispatchEvent( eventSelectChange );
      event.target.classList.add( selectClasses.itemActive );
      hideSelect();
    }
  }
} );

function hideSelect() {
  if ( $activeSelect ) {
    $activeSelect.classList.remove( selectClasses.open );
    $activeSelect = null;
  }
}

function getCustomSelectHtml( $element ) {
  const $options = $element.querySelectorAll( 'option' );
  let valueHtml = '';
  let listHtml = '';

  [ ...$options ].forEach( ( $option ) => {
    if ( $option.selected || $option.value === 'placeholder' ) {
      valueHtml = $option.innerHTML;
    }

    if ( $option.value !== 'placeholder' ) {
      listHtml += `<li data-value="${ $option.value }" 
class="${ selectClasses.item } ${ $option.selected ? selectClasses.itemActive : '' }">${ $option.innerHTML }</li>`;
    }
  } );

  return `
<div class="${ selectClasses.select }">
  <div class="${ selectClasses.value }">${ valueHtml }</div>
  <div class="${ selectClasses.box }">
    <ul class="${ selectClasses.list }">
      ${ listHtml }
    </ul>
  </div>
</div>
`;
}
