/* global ymaps */
import throttle from '../vendor/lodash-es/throttle';

const $map = document.getElementById( 'map' );
const throttleDelay = 50;
const fnThrottle = throttle( scrolling, throttleDelay );

if ( $map ) {
  scrolling();
}

window.mapInit = () => {
  const zoom = $map.getAttribute( 'data-zoom' ) || 16; // eslint-disable-line no-magic-numbers
  const coord1 = $map.getAttribute( 'data-coord' ).split( ',' );

  const myMap = new ymaps.Map( 'map', {
    zoom,
    center: coord1,
    controls: [ 'zoomControl', 'fullscreenControl', 'typeSelector', 'rulerControl' ],
  } );

  const myPlacemark1 = new ymaps.Placemark( coord1, {
    hintContent: 'ЮЛИЯ ЛАНДЫШЕВА',
  }, {
    iconLayout: 'default#image',
    iconImageHref: 'img/blocks/contacts/placeholder.svg',
    iconImageSize: [ 42, 42 ], // eslint-disable-line no-magic-numbers
    iconImageOffset: [ -21, -42 ], // eslint-disable-line no-magic-numbers
  } );

  myMap.behaviors.disable( 'scrollZoom' );
  myMap.geoObjects.add( myPlacemark1 );

  window.addEventListener( 'scroll', fnThrottle );
};

function scrolling() {
  // load map's script only if map block is visible
  if ( isElementInViewport( $map ) ) {
    window.removeEventListener( 'scroll', fnThrottle );
    loadScript();
  }
}

function loadScript() {
  const script = document.createElement( 'script' );

  script.type = 'text/javascript';
  script.src = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU&onload=mapInit';
  document.getElementsByTagName( 'head' )[ 0 ].appendChild( script );
}

function isElementInViewport( el ) {
  const rect = el.getBoundingClientRect();
  const vWidth = window.innerWidth || document.documentElement.clientWidth;
  const vHeight = window.innerHeight || document.documentElement.clientHeight;
  const efp = ( x, y ) => document.elementFromPoint( x, y );

  // Return false if it's not in the viewport
  if ( rect.right < 0 || rect.bottom < 0 ||
    rect.left > vWidth || rect.top > vHeight ) {
    return false;
  }

  // Return true if any of its four corners are visible
  return (
    el.contains( efp( rect.left, rect.top ) ) ||
    el.contains( efp( rect.right, rect.top ) ) ||
    el.contains( efp( rect.right, rect.bottom ) ) ||
    el.contains( efp( rect.left, rect.bottom ) )
  );
}

